}
@media (max-width: 992px) {
  .section-header.style-right .section-heading h3::before {
    transform: none;
    right: auto;
  }
}
.card .card-header{
  display: flex;
  flex-basis: 100%;
  order: 1;
  border-radius: 0;
}
.card .collapseparent{
  order: 2;
}
.accordion .card .collapseparent + .card-header .btn-link{
background: #ededed;
  border-color: #ededed;
  color: #000;
}
.accordion .card .collapseparent + .card-header .btn-link::before{
content: '\f067';
  background: #2d3e52;
  color: #ededed;
  font-family: "Font Awesome 5 Pro";
  padding: 0;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.accordion .card .collapseparent.show + .card-header .btn-link{
background: #01b7f2;
  color: #fff;
border-color: transparent;
}
.accordion .card .collapseparent.show + .card-header .btn-link::before{
background: #fff;
  color: #01b7f2;
content: '\f068';
}
ul li,
ul{
  list-style: none;
  padding: 0;
  margin: 0;
}
.group-form .custom-select:before{
  pointer-events: none;
}
.group-form .custom-select{
  padding-right: 0;
}
.custom-select select{
  appearance: none;
  border: 0;
  background: transparent;
  width: 100%;
}